import UtilIce from '@/libs/util-ice.js'
// 
import Index from './pages/Index'
import Login from './pages/Login'
import Error404 from './pages/Error404'
import Demo1 from './pages/Demo1'
import Demo2 from './pages/Demo2'
import HeaderAside from './layouts/HeaderAside'

//  routerConfig  iceworks 
// ice 
// 
//  ice 
// {
//   path: '/page4',
//   layout: d2LayoutMain,
//   component: 4
// }

//  name  path  name = page-demo1
//  UtilIce.recursiveRouterConfig  path2name 
// meta  Object.assign 
//  meta.name name  name 
//  

const routerConfig = [
  {
    path: '/',
    name: 'index',
    layout: HeaderAside,
    component: Index
  },
  {
    path: '/demo1',
    name: 'demo1', 
    layout: HeaderAside,
    component: Demo1,
    meta: {
      requiresAuth: true,
      title: ' 1'
    }
  },
  {
    path: '/demo2',
    layout: HeaderAside,
    component: Demo2
  }
];

// 
// ice 
// 
//  routerConfig

const routerConfigMenuOut = [
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {
      requiresAuth: false
    }
  },
  {
    path: '*',
    component: Error404
  }
]

// 
//  router.js 

export default UtilIce.recursiveRouterConfig([
  ...routerConfig,
  ...routerConfigMenuOut
])

// 
//  mian.js 

export const frameInRoutes = UtilIce.recursiveRouterConfig(routerConfig).map(e => {
  const route = e.children ? e.children[0] : e
  return {
    path: e.path,
    name: route.name,
    meta: route.meta
  }
})
